<script>
  export default {
    name: 'menu-top',
    data () {
      return {
        route: ''
      }
    },
    created () {
      this.route = this.$router.currentRoute.path
    },
    watch: {
      '$route.path': function (val) {
        this.route = val
      }
    },
    methods: {
      quit () {
        this.$router.replace('/login')
      },
      seeCompanyInfo () {
        layer.open({
          type: 1,
          title: ['企业名片使用方式', 'font-size:13px;color:#ffffff;background-color:#399ece;border-bottom:solid 1px #399ece;'],
          area: ['557px'],  //宽高
          content: $('#firm_card_content'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            $('#firm_card_content').css('display', 'none')
          }
        })
      }
    }
  }
</script>

